.designer-container {
  height: 100vh;
  width: 100vw;
}

/* Mobile Landscape */
@media only screen and (orientation: landscape) {
  .scene-container {
    width: 100vh !important;
    height: 100% !important;
    flex-shrink: 0;
    padding: 0px !important;
  }
  .interface-container {
    width: 100%;
    max-width: 500px;
    margin: auto;
    height: 100% !important;
  }
  #loading-screen {
    height: 100% !important;
  }
}

/* Desktop Landscape */
@media screen and (min-device-width: 1000px) and (orientation: landscape) {
  .scene-container {
    width: 100vh !important;
    height: 100%;
    flex-shrink: 0;
    padding: 0px !important;
  }
  .interface-container {
    max-width: 500px;
    margin: auto;
    height: 100% !important;
  }
  #loading-screen {
    height: 100% !important;
  }
}
